body {
	font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	margin: 0;
	padding: 0;
}

h1 {
	text-align: center;
}

.container {
	width: 1440px;
	margin: auto;
	height: 600px;
	display: flex;
}

.picture {
	width: 1440px;
	height: 100%;
	text-align: center;
	margin: 0 16px;
	border: 1px solid;
	border-radius: 24px;
}

.side-panel {
	text-align: center;
    width: 370px;
 	margin: 0 8px;
	border: 1px solid;
	height: 100%;
	border-radius: 24px;
}

.template-options-grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 24px;
	border: 1px solid;
	padding: 16px;
	border-radius: 16px;
	margin: 16px 46px;
	grid-auto-rows: minmax(24px, auto);
}

.template-option {
	height: 48px;
	width: 48px;
	border-radius: 4px;
	border: none;
	box-shadow: 1px 1px 1px 1px rgba(0,0,0,0.2);
	transition: box-shadow 0.4s ease-out;
}

.template-option:hover {
	cursor: pointer;
	box-shadow: 0px 10px 20px 2px rgba(0, 0, 0, 0.1);
}


.tooltip-container {
	display: flex;
    justify-content: center;
	transition: opacity 0.1s ease-out;
	opacity: 0;
	

}
.tooltip {
	position: relative;
	background-color: black;
	color: white;
	border-radius: 4px;
	padding: 4px;
    top: 50px;
	font-size: 14px;
}

.opacity {
	opacity: 1;
}

.tooltip:after {
	content: "";
	position:absolute;
	
	/* position tooltip correctly */
	left: 30%;
    /* margin-left: -33px; */
    top: -42%;
	/* vertically center */
	transform:translateY(-50%);
   
	/* the arrow */
	border:10px solid #000;
	border-color: transparent transparent black transparent;
}
  